.mainNavbar{
    display: flex;
    width: 100%;
    margin: auto;
    justify-content: space-between;
    background-color: white;
    border-bottom-right-radius: 1px;
    border-bottom-left-radius: 13px;
}
.searchInputContainer{
    display: flex;
    align-items: center;
    flex: 4;
    margin-top: 9px;
    background-color: aliceblue;
    padding: 5px;
    border-radius: 10px;
   
}
.IconsContainer{
    display: flex;
    align-items: center;
    flex: 0.22;
}
.LogoContainer{
    flex: 0.2;
}
.Icons{
    width: 20px;
    height: 20px;
    margin-left: 20px;
}
.searchIcons{
    width: 20px;
    height: 20px;
}
.ProfileImage{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-left: 20px;
}
.searchInput{
    width: 30pc;
    border: none;
    padding: 4px;
    border-radius: 10px;
    background-color: aliceblue;
}